"use client";

import { useState } from "react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { ArrowRight, ArrowLeft, Check } from "lucide-react";
import { gql, useMutation } from "@apollo/client"; // Import necessary methods from Apollo Client

const SUBMIT_ONBOARDING_MUTATION = gql`
  mutation SubmitOnboarding(
    $firstName: String!
    $lastName: String!
    $email: String!
    $role: String!
    $otp: String!
  ) {
    submitOnboarding(
      firstName: $firstName
      lastName: $lastName
      email: $email
      role: $role
      otp: $otp
    ) {
      success
      message
    }
  }
`;

export function OnboardingSteps() {
  const [step, setStep] = useState(1);
  const totalSteps = 4;
  const [role, setRole] = useState<"student" | "organizer" | "admin" | "">("");
  const [otp, setOtp] = useState(""); // OTP input

  // State for user data
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");

  const [submitOnboarding, { loading, error }] = useMutation(SUBMIT_ONBOARDING_MUTATION);

  const nextStep = async () => {
    if (step === 2 && otp !== "123456") {
      alert("Invalid OTP. Try again.");
      return;
    }

    if (step < totalSteps) {
      setStep(step + 1);
    } else {
      try {
        const response = await submitOnboarding({
          variables: {
            firstName,
            lastName,
            email,
            role,
            otp,
          },
        });

        if (response.data.submitOnboarding.success) {
          // Handle success (e.g., navigate to different pages)
          if (role === "student") {
            window.location.href = "/dashboard/student";
          } else if (role === "organizer") {
            window.location.href = "/dashboard/organizer";
          } else if (role === "admin") {
            window.location.href = "/dashboard/admin";
          }
        } else {
          alert(response.data.submitOnboarding.message);
        }
      } catch (err) {
        console.error("Error during onboarding:", err);
        alert("There was an error submitting your data. Please try again.");
      }
    }
  };

  const prevStep = () => {
    if (step > 1) {
      setStep(step - 1);
    }
  };

  return (
    <Card className="w-full max-w-md">
      <CardHeader className="space-y-1">
        <div className="flex items-center justify-between">
          <CardTitle className="text-2xl font-bold">
            {step === 1 && "Create an account"}
            {step === 2 && "Verify your email"}
            {step === 3 && "Choose your role"}
            {step === 4 && "Select your interests"}
          </CardTitle>
          <div className="text-sm text-muted-foreground">
            Step {step} of {totalSteps}
          </div>
        </div>
        <CardDescription>
          {step === 1 && "Enter your information to create your StudentHub account"}
          {step === 2 && "We’ve sent an OTP to your email. Please verify."}
          {step === 3 && "Select your role to personalize your experience"}
          {step === 4 && "Choose your interests to get personalized recommendations"}
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        {step === 1 && (
          <>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="first-name">First name</Label>
                <Input
                  id="first-name"
                  value={firstName}
                  onChange={(e) => setFirstName(e.target.value)}
                  placeholder="John"
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="last-name">Last name</Label>
                <Input
                  id="last-name"
                  value={lastName}
                  onChange={(e) => setLastName(e.target.value)}
                  placeholder="Doe"
                />
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input
                id="email"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                placeholder="john.doe@university.edu"
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input id="password" type="password" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="confirm-password">Confirm Password</Label>
              <Input id="confirm-password" type="password" />
            </div>
          </>
        )}

        {step === 2 && (
          <div className="space-y-4">
            <Label htmlFor="otp">Enter OTP sent to your email</Label>
            <Input
              id="otp"
              type="text"
              maxLength={6}
              placeholder="for now enter --> 123456"
              value={otp}
              onChange={(e) => setOtp(e.target.value)}
            />
            <p className="text-sm text-muted-foreground">
              Didn’t receive the OTP? <button className="text-blue-600 hover:underline">Resend</button>
            </p>
          </div>
        )}

        {step === 3 && (
          <div className="space-y-4">
            <div className="space-y-2">
              <Label>I am a</Label>
              <RadioGroup value={role} onValueChange={(val) => setRole(val)} className="grid gap-4 pt-2">
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="student" id="student" />
                  <Label htmlFor="student" className="flex flex-col">
                    <span className="font-medium">Student</span>
                    <span className="text-sm text-muted-foreground">
                      Access events, connect with peers, and build your portfolio
                    </span>
                  </Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="admin" id="admin" />
                  <Label htmlFor="admin" className="flex flex-col">
                    <span className="font-medium">University Admin</span>
                    <span className="text-sm text-muted-foreground">
                      Manage university events and approve student accounts
                    </span>
                  </Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="organizer" id="organizer" />
                  <Label htmlFor="organizer" className="flex flex-col">
                    <span className="font-medium">Event Organizer</span>
                    <span className="text-sm text-muted-foreground">
                      Create and manage events for students
                    </span>
                  </Label>
                </div>
              </RadioGroup>
            </div>
          </div>
        )}

        {step === 4 && (
          <div className="space-y-4">
            {/* Handle role-based information here */}
          </div>
        )}
      </CardContent>

      <CardFooter className="flex flex-col space-y-4">
        <div className="flex w-full justify-between">
          {step > 1 ? (
            <Button variant="outline" onClick={prevStep}>
              <ArrowLeft className="mr-2 h-4 w-4" /> Back
            </Button>
          ) : (
            <div></div>
          )}
          <Button onClick={nextStep}>
            {step < totalSteps ? (
              <>
                Next <ArrowRight className="ml-2 h-4 w-4" />
              </>
            ) : (
              <>
                Complete <Check className="ml-2 h-4 w-4" />
              </>
            )}
          </Button>
        </div>
        {step === 1 && (
          <div className="text-center text-sm">
            Already have an account?{" "}
            <Link href="/login" className="font-medium text-blue-600 hover:underline">
              Log in
            </Link>
          </div>
        )}
      </CardFooter>
    </Card>
  );
}
